import { Component, OnInit } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { UtilitiesService } from '../data/utilities.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-jobsearch',
  templateUrl: './jobsearch.component.html',
  styleUrls: ['./jobsearch.component.css'],
})
export class JobsearchComponent implements OnInit {
  private apiServer = this.utilitiesService.getApiUrl();
  jobsearch: Jobsearch[] = [];
  outTextOn = false;
  outText: string = 'outText';
  querytype = '0';
  ShowDesc = false;
  constructor(
    private http: HttpClient,
    private utilitiesService: UtilitiesService,
    private router: Router
  ) {}
  ngOnInit(): void {
    this.getAll();
  }
  onSelected(value:string): void {
		//this.outText = value;
		this.querytype = value;
    this.getAll();
	}
  onShowDesc(value): void{
   // this.outText += 'onHideDesc '+value;
    this.ShowDesc = value;
    this.getAll();
  }
  getAll() {
    let url = '/api/cronjob/show/'+this.querytype;
    console.log('service getAll() start ' + this.apiServer + url);
    const req = this.http.get<Jobsearch[]>(this.apiServer + url);
    req.subscribe((response) => {
      this.outText += 'sub';
      this.jobsearch = response;
    });
    //this.outText += 'end';
  }
  substr(str) {
    //return str.substring(0, 10);
    return str;
  }
}
class Jobsearch {
  search: string;
  title: string;
  desc: string;
  company: string;
  url: string;
  count: number;
  created: string;
  updated: string;
}
